    <showcode>
      <script type="syntaxhighlighter" class="brush:html;gutter: false;">
      <![CDATA[
      <!-- control buttons -->
      <button type="button" @click="disabled=true">Disabled</button>
      <button type="button" @click="disabled=false">Enabled</button>

      <v-selectpage :data="sample" :show-field="renderRow" :disabled="disabled">
      </v-selectpage>

      &lt;script&gt;
      export default {
        data(){
          return {
            disabled: false,
            renderRow(row){
              let result = '';
              if(row && Object.keys(row).length)
                result = `[<b>${row.id<10?'0'+row.id:row.id}</b>] - ${row.name} (${row.desc})`;
              return result;
            }
          };
        }
      };
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

