<script>
import country from './country'
import timezones from './timezones'
export default {
  data () {
    return {
      btnText: 'please select',
      selectResult: {},

      singleValue: '7',
      singleValueObj: null,
      multipleValue: '3,5,7',
      multipleValueObj: null,
      selected: '7',
      server: '1',
      disabled: false,
      renderRow (row) {
        let result = ''
        if (row && Object.keys(row).length) {
