    },
    getValue (obj) {
      return obj ? obj.value : ''
    },
    selectLogs (data) {
      if (data) this.selectResult = data
    },
    singleValues (val) {
      this.singleValueObj = val
    },
    multipleValues (val) {
      this.multipleValueObj = val
    },
    resultFormat (resp) {
      if (resp && resp.data) {
        let query = JSON.parse(resp.config.data)
        if (Object.keys(query).findIndex(val => val === 'searchKey') !== -1) {
          let list = resp.data.values.gridResult.list
          let row = list.find(val => String(val[query.searchKey]) === query.searchValue)
          return {
            totalRow: resp.data.values.totalRow,
            list: [row]
          }
        } else return resp.data.values.gridResult
      }
    }
  },
